						'<polygon fill="#010101" points="17.1,1.1 16,0 8.5,7.5 1.1,0 0,1.1 7.5,8.5 0,16 1.1,17.1 8.5,9.6 16,17.1 17.1,16 9.6,8.5 "/>'+
						'</svg></a>'+
					'<div id="popup-content-area">'+
					'	<span class="popup-loading">Loading...</span>'+
					'</div>'+
					'<div class="clear"></div>'+
				'</div>';
